$tablet-width: 660px;
$desktop-width: 960px;

.app-download {
    height: 668px;
    display: flex;
    flex-direction: column;
}

.app-download__wrapper {
    height: 668px;
}

.app-download__title-wrapper {
    height: 398px;
}

.app-download__title {
    display: none;
}

.btn {
    width: 282px;
    height: 55px;
    border-radius: 50px;
    border: 3px solid black;
    padding: 14px 30px 14px 30px;
    margin-left: 25%;
}

.app-download__site {
    padding-top: 40px;
    list-style: none;
    display: flex;
    flex-direction: row;
}

@media (min-width: $tablet-width) {
    .app-download {
        height: 620px;
    }

    .app-download__title-wrapper {
        width: 311px;
        height: 152px;
        padding: 206px 0px 34px 0px;
    }

    .app-download__title {
      width: 251px;
      height: 92px;
        display: block;
        padding: 0px 30px 0px 30px;
        text-align: center;
        margin: 0;
    }

    .btn{
        // border-radius
        padding: 14px 30px 14px 30px;
        text-align: center;
        margin-left: 40px;
    }

    .app-download__site {
        width: 210px;
        margin-top: 40px;
        margin-left: 40px;
        padding: 0;
        justify-content: space-around;
    }

}

@media (min-width: $desktop-width) {
    .app-download__title-wrapper {
        padding: 120px 0px 60px 0px;
        margin-left: 80px;
    }
    .btn {
        margin-left: 130px;
    }

    .app-download__site {
        margin-top: 60px;
        margin-left: 170px;
    }
}

